.game {
    border: white solid 2px;
    border-radius: 5px;
    background-color: rgba(204, 204, 204, 0.5);
    padding: 15px;
    margin: 15px 7.5px;
}

.board {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 50vh;
    height: 50vh;
}

.winMessage {
    display: flex;
    justify-content: center;
    font-weight: bold;
    text-transform: uppercase;
    box-sizing: border-box;
    padding: 20px;
}
